
.player {
  padding-bottom: 20px;
  padding-top: 20px;
}
.player .info {
  text-align: left;
}
.player .score {
  color: #d0c1c1
}
.player .lastScore {
  opacity: 0;
  position: absolute;
  margin-left: -20px;
}
.fade-enter {
  opacity: 1 !important;
}
.fade-enter-to {
  -webkit-transform: scale(10, 10);
          transform: scale(10, 10);
}
.fade-enter-active {
  transition: all 1.5s !important;
}



